<template>
  <div class="chatBtms">
    <ul class="chatMenuList">
      <li><a role="button" @click="onHistory()">{{ $t('chatbot_footer.chat_history') }}</a></li>
      <!-- <li><a role="button" @click="$router.push(`/${$lang}/history`)">{{ $t('chatbot_footer.chat_history') }}</a></li> -->
    </ul>
    <div class="chatMenu">
      <button type="button" @click="$uiHandler.handleMenu">
        <span></span>
        <span></span>
        <span></span>
      </button>
    </div>
    <div class="chatingArea">
      <input
          @keyup.enter="$store.dispatch('SEND_MESSAGE', message)"
          @input="$store.commit('UPDATE_MESSAGE', $event.target.value)"
          :disabled="loading || disable"
          :value="message"
          title="글입력"
          ref="message-input"
          :placeholder="$t('chatbot_footer.message_input_placeholder')"
          maxlength="256"
      />
      <button type="submit" @click="$store.dispatch('SEND_MESSAGE', message)">보내기</button>
    </div>
  </div>
</template>

<script type="text/javascript" src="</js/m_UI.js"></script>
<script>
import store from "@/vuex/store";
import $ from "jquery";
export default {
  name: 'ChatbotFooter',
  store,
  isPc:false,
  mounted:function(){
    var filters = "win16|win32|win64|mac|macintel";
    if (navigator.platform && filters.indexOf(navigator.platform.toLowerCase()) > 0) {
      //this.$refs['message-input'].focus();
      this.isPc = true;
    }
  },
  computed: {
    message: function() {
      return this.$store.state.message;
    },
    disable: function () {
      return this.$store.state.disableChat;
    },
    loading: function() {
      if (this.$store.state.mode === this.$dictionary.writerType.COUNSELOR) {
        return false;
      }

      return this.$store.state.chatLoading.visible;
    }
  },
  watch: {
    loading: function (current, old) {
      if (!current && old) {
        this.$nextTick(() => {
          //var filters = "win16|win32|win64|mac|macintel";
          //if (navigator.platform && filters.indexOf(navigator.platform.toLowerCase()) > 0)
          if(this.isPc)
          {
            this.$refs['message-input'].focus();
          }
        });
      }
    }
  },
  methods:{
    onHistory() {
      this.$router.push(`/${this.$lang}/cb/history`);
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_15);
    },
  },
}
</script>