<template>
  <div>
    <!-- layer popup start -->
    <section id="popup_chat" class="layPopFull layPopChat" tabindex="0">
      <ChatbotHeader />
      <div id="chatContainer" class="chatContainer" v-bind:style="{paddingBottom:isProMax?'120px':''}" ><!--비활성시 class disabled 추가-->
        <Chat :conversation="$store.state.conversation" isScrollDown />
        <ChatbotFooter />
      </div>
      <ConsultingChatToast />
    </section>
    <!-- layer popup end -->

    <div id="layerPopupContainer"></div><!-- layer popup 삽입위치 -->
    <SystemPopup />
    <Loading />
    <SlidePopup />
  </div>
</template>

<script>
import Loading from '@/components/Loading';
import ChatbotHeader from '@/components/ChatbotHeader';
import ChatbotFooter from '@/components/ChatbotFooter';
import SystemPopup from '@/project/components/SystemPopup';
import Chat from '@/components/Chat';
import store from '@/vuex/store';
import ConsultingChatToast from '@/project/components/ConsultingChatToast';
import getCookie from '@/utils/getCookie';
import SlidePopup from '../components/SlidePopup.vue';

export default {
  name: 'chatbot',
  store,
  data() {
    return {
      isProMax: false,
    };
  },
  mounted() {
    // 사용자 유효동작 감시 타이머
    this.resetTimer();
    this.$uiHandler.listenClick(this.resetTimer);

    // 로그인 여부 체크
    const uid = getCookie('APP_SID');
    if (uid === '') {
      // window.location = this.$dictionary.urls.LOGIN;
      // return;

      // for test
      alert('로그인상태가 아님. 테스트모드로 진행');
      // document.cookie = 'APP_SID=MTIwMTAwMDAwMTY5NDM2Mw=='; // 로그인 없이 테스트용 코드
    }

    this.$analyticsHandler.visitApply();
    this.$uiHandler.faceOutLoading();

    // iphone 11pro Max 레이아웃 문제
    {
      const UserAgent = navigator.userAgent;
      if (UserAgent.match(/iPhone/i) != null) {
        if (window.screen.height >= 896) {
          this.isProMax = true;
        }
      }
    }
  },
  computed: {
    showMap() {
      return this.$store.state.showMap;
    },
  },
  methods: {
    bottom() {

    },
    resetTimer() {
      this.$store.commit('RESET_TIMER');
    },
  },
  components: {
    ConsultingChatToast,
    SystemPopup,
    ChatbotFooter,
    ChatbotHeader,
    Loading,
    Chat,
    SlidePopup,
  },
};

</script>

