<template v-if="info.hasWrapper">
  <div
      class="chatTxts"
  >
    <component :is="component" :data="info.data" v-if="component" :intent="info.intent" />
  </div>
</template>
<template v-else>
  <component :is="component" :data="info.data" v-if="component" :intent="info.intent" />
</template>
<script>
export default {
  name: 'DynamicLoader',
  props: ['info'],
  data() {
    return {
      component: null,
    };
  },
  methods:{
    loader() {
      console.log('info', this.info);

      if (!this.info.type) {
        return null;
      }

      return () => import(`@/${this.info.type}`);

      // if (!this.info.name) {
      //   return null;
      // }

      // return () => import(`@/${this.info.name}`);
    },
  },  
  computed: {
    // f() {
    //   console.log('computed');
    //   return this.loader();
    // },
    // loader() {
    //   if (!this.info.name) {
    //     return null;
    //   }

    //   return () => import(`@/components/${this.info.name}`);
    // },
  },
  updated() {
    if (window.initAllAtOnce) {
      window.initAllAtOnce();
    }
  },
  mounted() {
    if(this.loader){
      this.component = this.loader();
    }
  },
};
</script>