import dictionary from '@/dictionary';
import enomixHandlers from '@/utils/enomixHandlers';
import chatbotChatHandler, { counselorChatHandler } from '@/utils/chatbotChatHandler';
import { defaultConversation } from '@/vuex/chatbot';
import translator from '@/assets/messages';
import language from '@/utils/getLanguage';
import commonHandler from '@/utils/commonHandler';

const MAX_REPEAT_CALL_ERROR = 3;

const SURVEY_STATUS = {
    start: 'ON',
};

const enomix = {
    state: {
        seq: 1,
        talkId: null,
        visibleToast: false,
        surveyCallback: null,
        loadedEnomix: false,
        delayConsultingCount: 0,

        history: [],
        conversation: [],
        conversation_date: '',
        total: 0,
        surveyStatus: null,
    },
    mutations: {
        SURVEY_STATUS_END(state) {
            state.surveyStatus = null;
        },
        SURVEY_STATUS_START(state) {
            state.surveyStatus = SURVEY_STATUS.start;
        },

        UPDATE_ENOMIX(state) {
            state.loadedEnomix = true;
        },

        TOGGLE_TOAST_VISIBLE(state) {
            state.visibleToast = !state.visibleToast;
        },

        UPDATE_SEQ(state, newSeq) {
            state.seq = newSeq;
        },

        UPDATE_TALK_ID(state, id) {
            state.talkId = id;
        },

        UPDATE_SURVEY_CALLBACK(state, callback) {
            state.surveyCallback = callback;
        },

        RESET_CONSULTING_INFO(state) {
            state.seq = 1;
            state.talkId = null;
            state.visibleToast = false;
            state.surveyCallback = null;
        },

        ADD_DELAY_CONSULTING_COUNT(state) {
            state.delayConsultingCount += 1;
        },

        UPDATE_HISTORY(state, history) {
            const newHistory = [...state.history, ...history];
            state.history = newHistory;
        },

        UPDATE_TOTAL(state, total) {
            state.total = total;
        },

        UPDATE_ENOMIX_CONVERSATION(state, conversation) {
            state.conversation = conversation;
        },

        UPDATE_ENOMIX_CONVERSATION_DATE(state, date) {
            state.conversation_date = date;
        },
    },
    actions: {
        CLEAR_ENOMIX_HISTORY_DETAIL(context) {
            context.commit('UPDATE_ENOMIX_CONVERSATION', []);
            context.commit('UPDATE_ENOMIX_CONVERSATION_DATE', '');
        },

        async GET_HISTORY_DETAIL(context, talkId) {
            const result = await enomixHandlers.getHistoryDetail({ talkId });
            if (!result) {
                return;
            }

            context.commit('UPDATE_ENOMIX_CONVERSATION_DATE', result.data.cdate);
            context.commit('UPDATE_ENOMIX_CONVERSATION', commonHandler.parseConversationFromEnomix(result.data.conversations));
        },

        async GET_HISTORY(context, options) {
            const { pageNo } = options;
            const { rowsPerPage } = options;
            const result = await enomixHandlers.getHistory({ pageNo, rowsPerPage });
            if (!result) {
                return;
            }

            context.commit('UPDATE_HISTORY', result.data.list);
            context.commit('UPDATE_TOTAL', result.data.total);
        },

        RE_START_CHATBOT(context, data) {
            context.commit('ADD_CONVERSATION', counselorChatHandler(data.message));
            context.commit('ADD_CONVERSATION', defaultConversation);
            context.commit('UPDATE_MODE', dictionary.writerType.CHATBOT);
            context.commit('SURVEY_STATUS_END');
            context.commit('RESET_TIMER');
        },

        async EXIT_CONSULTING(context) {
          const DEFAULT_EXIST_MESSAGE = '상담이 종료되어 챗봇으로 전환됩니다.';
          if (!context.state.talkId) {
            context.commit('RESET_CONSULTING_INFO');
            context.dispatch('RE_START_CHATBOT', { message: DEFAULT_EXIST_MESSAGE });
            return;
          }

          const result = await enomixHandlers.exitConsulting(context.state.talkId, context.state.seq);
          if (!result) {
            return;
          }

          if (result.data.existAfterSurvey) {
            context.dispatch('START_SURVEY', result.data);
            return;
          }

          context.commit('RESET_CONSULTING_INFO');
          context.dispatch('RE_START_CHATBOT', { message: result.data.message });
        },

        async HANDLE_DELAY_CONSULTING(context, { data, disableContentMessage }) {
            context.commit('HIDE_LOADING');

            if (context.state.delayConsultingCount < MAX_REPEAT_CALL_ERROR) {
                context.commit('ADD_CONVERSATION', chatbotChatHandler(disableContentMessage));
                context.commit('ADD_DELAY_CONSULTING_COUNT');
            } else {
                const result = await enomixHandlers.getErrorTemplate();
                if (!result) {
                    return;
                }

                context.commit('ADD_CONVERSATION', chatbotChatHandler(result.data.disableContentMessage));
                return;
            }

            const minute = 6 * 10 * 1000;
            const callConnectionOfConsulting = () => {
                context.dispatch('HANDLE_CONSULTING', data);
            };

            setTimeout(callConnectionOfConsulting, minute);
        },

        async SEND_SURVEY_RESULT(context, data) {
            const result = await enomixHandlers.sendSurvey(context.state.talkId, data.score, data.message);
            if (!result) {
                return false;
            }

            if (!result.data.isSuccess) {
                context.dispatch('CALL_SYSTEM_ERROR', { message: translator[language].systemError.network_error });
                return false;
            }

            if (typeof context.state.surveyCallback === 'function') {
                context.state.surveyCallback();
                context.commit('RESET_CONSULTING_INFO');
            }

            return true;
        },

        START_SURVEY(context, data) {
            context.commit('SURVEY_STATUS_START');
            context.commit('DISABLE_CHAT');
            context.commit('ADD_CONVERSATION', chatbotChatHandler(data.surveyMessage));
            context.commit('UPDATE_SURVEY_CALLBACK', () => {
                context.dispatch('RE_START_CHATBOT', data);
                context.commit('ABLE_CHAT');
            });
        },

        async SEND_CHAT_TO_COUNSELOR(context, { msg: message, callback }) {
            // 상담사 채팅 만족도 조사 중에 발화는 무시한다.
            if (context.state.surveyStatus === SURVEY_STATUS.start) {
                return;
            }

            // 
            let newSequence = context.state.seq;
            const result = await enomixHandlers.sendTalk(context.state.talkId, newSequence, message);

            if (!result.data.data.messages) // 메시지가 없다면 
            {
                newSequence = result.data.data.seq; // 시퀀스값은 그대로
            } else // 메시지가 있는 경우  
            {
                // 받아온 메시지 배열의 마지막 요소의 seq 를 최종 시퀀스로...
                newSequence = result.data.data.messages[result.data.data.messages.length - 1].seq; // 시퀀스값 갱신
            }
            callback(); // 사용자 입력값을 발화로 표시 // context.commit('ADD_CONVERSATION', userChatHandler(msg)) 
            context.commit('UPDATE_SEQ', newSequence); // 시퀀스값 갱신
        },

        async RECEIVE_CHAT_FROM_COUNSELOR(context, { talkId, isExit }) {
            let newSequence = context.state.seq;
            const result = await enomixHandlers.receiveTalk(talkId, newSequence);
            if (!result) {
                return;
            }
            if (!result.data.messages) {
                newSequence = result.data.seq;
            } else {
                newSequence = result.data.messages[result.data.messages.length - 1].seq;
            }
            context.commit('UPDATE_TALK_ID', talkId);
            context.commit('UPDATE_SEQ', newSequence);
            context.commit('HIDE_LOADING');

            if (result.data.existAfterSurvey) {
                await enomixHandlers.sendConsultingToVOC({ talkId });
                context.dispatch('START_SURVEY', result.data);
                return;
            }

            if (context.state.visibleToast) {
                context.commit('TOGGLE_TOAST_VISIBLE');
                context.commit('ABLE_CHAT');
            }

            if (isExit) {
                await enomixHandlers.sendConsultingToVOC({ talkId });
                context.commit('RESET_CONSULTING_INFO');
                context.dispatch('RE_START_CHATBOT', result.data);
                return;
            }

            context.commit('ADD_CONVERSATION', counselorChatHandler(result.data.message));
        },
        COUNSELOR_ON_CHAT(context) {
            context.commit('SHOW_LOADING');
        },

        // 상담하기
        async HANDLE_CONSULTING(context, data) {
            context.commit('DISABLE_CHAT');
            context.commit('SHOW_LOADING', dictionary.writerType.CHATBOT);

            // api TalkAvailable 
            const result = await enomixHandlers.getConsultingAvailability();
            if (!result) {
                return;
            }

            const {
             isAvailable, message, isDelayConsulting, disableContentMessage,
            } = result.data;

            // TalkAvailable의 결과가 
            if (isDelayConsulting) {
                context.dispatch('HANDLE_DELAY_CONSULTING', { data, disableContentMessage });
                return;
                // 종료
            }

            if (isAvailable) // 상담가능
            {
                // 카테고리 가져오기 
                const category = await enomixHandlers.getCategory();
                if (!category) {
                    return;
                }

                // 시작하기
                const talkStartResult = await enomixHandlers.startTalk(category.data.nodeId, data.text, data.previousConversation);

                if (!context.state.loadedEnomix) //gateway 로딩 안된 경우
                {
                    enomixHandlers.loadEnomix(talkStartResult.data.customerId);
                }

                context.commit('UPDATE_TALK_ID', talkStartResult.data.talkId); // id 업데이트
                context.commit('UPDATE_ENOMIX'); // 플래그 켜기

                // ui
                context.commit('HIDE_LOADING');
                context.commit('ADD_CONVERSATION', chatbotChatHandler(message));
                context.commit('UPDATE_MODE', dictionary.writerType.COUNSELOR);
                context.commit('TOGGLE_TOAST_VISIBLE');
                return;
                // 종료
            }

            context.commit('ABLE_CHAT');
            context.commit('HIDE_LOADING');
            context.commit('ADD_CONVERSATION', chatbotChatHandler(disableContentMessage));
        },
    },
};

export default enomix;