import dictionary from '@/dictionary';

const system = {
    state: {
        type: dictionary.systemPopupType.EXIT,
        errorMessage: '',
    },
    mutations: {
        UPDATE_TYPE(state, type) {
            state.type = type;
        },
        UPDATE_ERROR_MESSAGE(state, message) {
            state.errorMessage = message;
        },
    },
    actions: {
        CALL_SYSTEM_ERROR(context, { message }) {
            context.commit('UPDATE_TYPE', dictionary.systemPopupType.SYSTEM_ERROR);
            context.commit('UPDATE_ERROR_MESSAGE', message);
        },
        RESET_CALL_SYSTEM_ERROR(context) {
            context.commit('UPDATE_TYPE', dictionary.systemPopupType.EXIT);
            context.commit('UPDATE_ERROR_MESSAGE', '');
        },
        CALL_USER_LOGOUT(context) {
            context.commit('UPDATE_TYPE', dictionary.systemPopupType.LOGOUT);
            context.commit('UPDATE_ERROR_MESSAGE', '');
        },
    },
};

export default system;