import languageType from '@/dictionary/languageType';

export default (to, from, next) => {
    if (to.fullPath === '/' || ['/cb', '/cb/'].includes(to.fullPath)) {
        next('/cb/kr');
        return;
    }

    if (!to.params.lang) {
        next('/cb/kr');
        return;
    }

    const isAbnormalUrl = ![languageType.KR, languageType.CN].includes(to.params.lang);
    if (isAbnormalUrl) {
        const newPath = to.fullPath.replace(to.params.lang, languageType.KR);
        next(newPath);
        return;
    }

    next();
};