// 전체 로딩을 종료하는 함수
import $ from 'jquery';
// import store from '../../vuex/store';

const handlerType = {
    cm_faceOutLoading: 'cm_faceOutLoading',
    cm_handleMenu: 'cm_handleMenu',
    cm_showClosePopup: 'cm_showClosePopup',
    cm_hideClosePopup: 'cm_hideClosePopup',
    cm_downScroll: 'cm_downScroll',
    cm_listenClick: 'cm_listenClick',
    cm_swiperNextPrevShow: 'cm_swiperNextPrevShow',
    // POPUP_SLIDE: 'POPUP_SLIDE',
}

const main = (type, obj) => {
    console.log('uiHandler', type, obj);
    switch (type) {
        case handlerType.cm_faceOutLoading:
            faceOutLoading();            
            break;
        case handlerType.cm_showClosePopup:
            showClosePopup();        
            break;
        case handlerType.cm_handleMenu:
            handleMenu();
            break;
        case handlerType.cm_hideClosePopup:
            hideClosePopup();
            break;
        case handlerType.cm_downScroll:
            downScroll(obj);
            break;
        case handlerType.cm_listenClick:
            listenClick(obj);
            break;
        case handlerType.cm_swiperNextPrevShow:
            swiperNextPrevShow();
            break;
        // case handlerType.POPUP_SLIDE:
        //     store.commit('POPUP_SLIDE', obj.data);
        //     break;    
    }
}

const faceOutLoading = () => {
    setTimeout(() => {
        $('.chatLoading').fadeOut(); // eslint-disable-line
    }, 1000);
};

// 하단 좌측 버튼 메뉴에 대한 토글처리하는 함수
const handleMenu = () => {
    $('.chatBtms').toggleClass('on');
};

// 시스템 팝업을 보이게하는 함수
const showClosePopup = () => {
    $('.chatPops').show();
    $('#system-alarm-dimmed').show();
};

// 시스템 팝업을 닫게하는 함수
const hideClosePopup = () => {
    $('.chatPops').hide();
    $('#system-alarm-dimmed').hide();
};

// 발화 시 스크롤이 항상 아래로 최신화되도록 수정해주는 함수
const downScroll = (scrollHeight) => {
    $('.chatContents').animate({ scrollTop: scrollHeight || $('.chatContents').prop('scrollHeight') }, 'slow');
    return $('.chatContents').prop('scrollHeight');
};

const swiperNextPrevShow = () => {
    const filters = 'win16|win32|win64|mac|macintel';
    console.log(navigator.platform, filters.indexOf(navigator.platform.toLowerCase()) > 0);
    if (navigator.platform && filters.indexOf(navigator.platform.toLowerCase()) > 0) {
        $('.swiper-button-prev, .swiper-button-next').show();
    }
};

const listenClick = (f) => {
    $(document).click(() => {
        f();
    });
};

const closePopup = () => {
    document.getElementById("myNav").style.height = "0%";
}

export default {
    handlerType,
    faceOutLoading,
    handleMenu,
    showClosePopup,
    hideClosePopup,
    downScroll,
    swiperNextPrevShow,
    listenClick,
    closePopup,
    main,
};