import chatbotChatHandler, { userChatHandler, counselorChatHandler } from '@/utils/chatbotChatHandler';
import language from '@/utils/getLanguage';
import dictionary from '@/dictionary';

class Service {
    parseLineBreakToBr(value) {
      return `${value}`.replace(/(?:\r\n|\r|\n)/g, '<br>');
    }

    linkify(inputText) {
        let replacedText;

        // URLs starting with http://, https://, or ftp://
        const replacePattern1 = /(\b(https?|ftp):\/\/[-A-Z0-9+&@#/%?=~_|!:,.;]*[-A-Z0-9+&@#/%=~_|])/gim;
        replacedText = inputText.replace(replacePattern1, '<a href="$1" target="_blank" class="text-link">$1</a>');

        // URLs starting with "www." (without // before it, or it'd re-link the ones done above).
        const replacePattern2 = /(^|[^/])(www\.[\S]+(\b|$))/gim;
        replacedText = replacedText.replace(replacePattern2, '$1<a href="http://$2" class="text-link" target="_blank">$2</a>');

        // Change email addresses to mailto:: links.
        const replacePattern3 = /(([a-zA-Z0-9\-_.])+@[a-zA-Z_]+?(\.[a-zA-Z]{2,6})+)/gim;
        replacedText = replacedText.replace(replacePattern3, '<a href="mailto:$1" class="text-link">$1</a>');

        return replacedText;
    }

    translateTimePivot(pivot) {
        if (!pivot) {
            return '';
        }

        if (`${pivot}`.toLocaleLowerCase() === 'am') {
            return window.language === dictionary.languageType.KR ? '오전' : '上午';
        }

        return window.language === dictionary.languageType.KR ? '오후' : '下午';
    }

    parseDateFromEnomix(date) {
        if (!date) {
            return {
                year: '',
                month: '',
                day: '',
                hour: '',
                minute: '',
                pivot: '',
            };
        }
        const year = date.substr(0, 4);
        const month = date.substr(4, 2);
        const day = date.substr(6, 2);
        const hour = date.substr(8, 2);
        const minute = date.substr(10, 2);
        let pivot = '';
        if (hour >= 12) {
            pivot = language === dictionary.languageType.KR ? '오후' : '下午';
        } else {
            pivot = language === dictionary.languageType.KR ? '오전' : '上午';
        }

        return {
            year,
            month,
            day,
            hour: hour > 12 ? hour - 12 : hour,
            minute,
            pivot,
        };
    }

    parseConversationFromEnomix(messages) {
        const FLAGS = {
            C: 'C',
            A: 'A',
            S: 'S',
        };
        return messages.map((message) => {
            const { msg, cdate, flag } = message;
            const { hour, minute, pivot } = this.parseDateFromEnomix(cdate);

            switch (flag) {
                case FLAGS.A:
                    return counselorChatHandler(msg, pivot, `${hour}:${minute}`);
                case FLAGS.S:
                    return chatbotChatHandler(msg, pivot, `${hour}:${minute}`);
                default:
                    return userChatHandler(msg, pivot, `${hour}:${minute}`);
            }
        });
    }
}

export default new Service();