import Vue from 'vue';
import Router from 'vue-router';
import Chatbot from '@/pages/Chatbot';
import ChatbotHistory from '@/pages/ChatbotHistory';
import ChatbotHistoryDetail from '@/pages/ChatbotHistoryDetail';
import redirect from '@/utils/redirect';

Vue.use(Router);

export const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            beforeEnter: redirect,
        },
        {
            path: '/cb',
            beforeEnter: redirect,
        },
        {
            path: '/cb/:lang',
            name: 'chatbot',
            component: Chatbot,
            beforeEnter: redirect,
        },
        {
            path: '/cb/:lang/history',
            name: 'chatbotHistory',
            component: ChatbotHistory,
            beforeEnter: redirect,
        },
        {
            path: '/cb/:lang/history/:talkId',
            name: 'chatbotHistoryDetail',
            component: ChatbotHistoryDetail,
            beforeEnter: redirect,
        },
        {
            path: '*',
            beforeEnter: redirect,
        },
    ],
});

export default router;