import dictionary from '@/dictionary';
import uiHandler from '@/utils/uiHandlers';
import chatbotChatHandler, { userChatHandler } from '@/utils/chatbotChatHandler';
import http from '@/utils/axios';
import language from '@/utils/getLanguage';
// import translator from '@/assets/messages';
import callback from '@/project/callback';

const apiUrl = `http://${window.location.hostname}:8080/cb/api/chatbot`;

export const defaultConversation = chatbotChatHandler([
    {
        type: dictionary.componentType.Intro,
    },
]);

export default {
    state: {
        scrollHeight: 0,
        conversation: [],
        message: '',
        options: {},
        chatLoading: {
            visible: false,
            className: 'chatType01',
        },
        disableChat: false,
        mode: dictionary.writerType.CHATBOT,
        mapUrl: '',

        orderTimeSearchModal_old: null,
        orderTimeSearchModal_depaPlacNm: '',
        orderTimeSearchModal_departurePlace: '',
        orderTimeSearchModal_date: '',
        orderTimeSearchModal_time: '',
        timerId: null,
        popup: { 
            component:'project/components/Login',
        },
    },
    mutations: {
        SAVE(state, obj) {
            console.log('SAVE', obj)
            if( ('localStorage' in window) && window['localStorage'] !== null )
            {            
                // alert('support')
            } else {
                alert('not support')
                return;
            }

            const json = JSON.stringify(obj)
            console.log('json', json);
            localStorage.setItem('chatbot', json);

            const value = localStorage.getItem('chatbot');
            console.log('saved : ', value);
        },
        SET_SCROLL_HEIGHT(state, value) {
            state.scrollHeight = value;
        },
        SET_SET_ORDERTIME_TIME(state, time) {
            state.orderTimeSearchModal_time = time;
        },
        SET_SET_ORDERTIME_DATE(state, date) {
            console.log('SET_SET_ORDERTIME_DATE')
            state.orderTimeSearchModal_date = date;
        },
        SET_SET_ORDERTIME_PLACE(state, place) {
            state.orderTimeSearchModal_departurePlace = place;
        },
        SET_ORDERTIME_DLG(state, payload) {
            if (typeof payload === 'undefined') {
                state.orderTimeSearchModal_old = null;
                state.orderTimeSearchModal_departurePlace = ''; //  "0035";
                state.orderTimeSearchModal_date = ''; // "2020-01-01",
                state.orderTimeSearchModal_time = ''; // "PM 07:15";

                return;
            }
            state.orderTimeSearchModal_old = payload;
          /*
            {
              depaPlacNm: '',
              dpatExptDttm: '2020-09-24 12:40',
              openNm: '편명',
              drflYn: 'Y',
              ordNo: '',
              ordCnt: 2,
              exitPossYn: 'Y',
              regSeq: 1
            }
          */

            const arr = payload.dpatExptDttm.split(' ');
            let date = arr[0];
            date = date.replace(/-/g, '.');

            let time = 'AM ';
            {
                const temp = arr[1];
                const array = temp.split(':');
                let hh = array[0];
                if (Number(hh) > 12) {
                    time = 'PM ';
                    hh -= 12;
                }

                const mm = array[1];
                time = `${time + hh }:${ mm}`;
            }

            const { depaPlacNm } = payload;
            const place = dictionary.deptCode.codes[depaPlacNm];

            state.orderTimeSearchModal_depaPlacNm = depaPlacNm;
            state.orderTimeSearchModal_departurePlace = place; //  "0035";
            state.orderTimeSearchModal_date = date; // "2020-01-01",
            state.orderTimeSearchModal_time = time; // "PM 07:15";
            // console.log("state>", state)
        },
        POPUP_SLIDE(state, data){
            console.log('POPUP_SLIDE', data);
            state.popup = data;
            document.getElementById("myNav").style.height = "100%";
        },
        SHOW_MAP(state, url) {
            console.log('SHOW_MAP'),
            state.mapUrl = url;
        },
        ABLE_CHAT(state) {  // 챗가능상태
            state.disableChat = false;
        },
        DISABLE_CHAT(state) {
            state.disableChat = true;
        },
        UPDATE_OPTIONS(state, options) {    // 범용
            state.options = options;
        },
        UPDATE_MESSAGE(state, msg) {
            state.message = msg;
        },
        UPDATE_MODE(state, newMode) {
            state.mode = newMode;
        },
        ADD_CONVERSATION(state, conversation) {
            console.log('ADD_CONVERSATION', conversation);
            state.conversation.push(conversation);
            this.commit('RESET_TIMER');
        },
        HIDE_LOADING(state) {
            state.chatLoading.visible = false;
        },
        SHOW_LOADING(state, type) {
            state.chatLoading.visible = true;
            uiHandler.downScroll();

            switch (type) {
                case dictionary.writerType.COUNSELOR:
                    state.chatLoading.className = 'chatType01';
                    break;

                default:
                    state.chatLoading.className = 'chatType02';
            }
        },
        RESET_TIMER(state) {
            if (state.timerId) {
                clearTimeout(state.timerId);
            }

            state.timerId = setTimeout(() => {
                callback.timeOut();
            }, 
            30 * 60 * 1000 // config
            ); // 30min
        },
    },
    actions: {
        async LOAD(context, payload) {
            console.log('LOAD', payload)
            if( ('localStorage' in window) && window['localStorage'] !== null )
            {            
                // alert('support')
            } else {
                alert('not support')
                return;
            }

            let value = '';
            const {response, key} = payload;
            if(key){
                value = localStorage.getItem(key);
            }
            else{
                value = localStorage.getItem('chatbot');
            }
            console.log('load : ', value);

            // 서버에 전송한다.        
            this.dispatch('CALL_RESPONSE',
                {
                    response,
                    params: value,
                }
            );
        },
        // 프론트에서 발화/서비스 핸들러 호출이 아닌 기간계 데이터를 요청하는데 사용한다.        
        async POST(context, payload) {
            console.log('POST>', payload);

            const options = {...payload};
            const { data } = await http.post(
                apiUrl, {options}
            );
            return data;
        },

        // 서비스 직접 호출
        async CALL_RESPONSE(context, payload) {
            // 사용자 입력창 초기화
            console.log('CALL_RESPONSE', payload);
            /*
                payload : {
                    response,
                    params,
                    text: '...',            
                }
            */

            const {text} = payload;
            if(text){ // 발화 내용이 있다면 출력
                context.commit('ADD_CONVERSATION', userChatHandler(text));
            }            

            // 로딩 표시
            context.commit('SHOW_LOADING', dictionary.writerType.CHATBOT);

            // 챗봇 서버에 메시지 전달
            const { data } = await http.post(
                // '/api/chatbot', 
                apiUrl,
            {
                lang: language,
                options: payload,
            });

            console.log('CALL_RESPONSE.data', data);

            // 숨기고 
            context.commit('HIDE_LOADING'); 

            // 서버응답 표시
            context.commit('ADD_CONVERSATION', chatbotChatHandler(data.contents)); // 발화표시

            // context.commit('UPDATE_OPTIONS', data.options || null);
        },        

        // 서비스 직접 호출
        async SEND_INTENT(context, payload) {
            // 사용자 입력창 초기화
            console.log('SEND_INTENT', payload);
            /*
                payload : {
                    intent,
                    params,
                    text: '...',            
                }
            */

            const {text} = payload;
            if(text){ // 발화 내용이 있다면 출력
                context.commit('ADD_CONVERSATION', userChatHandler(text));
            }            
    
            // 로딩 표시
            context.commit('SHOW_LOADING', dictionary.writerType.CHATBOT);

            // 챗봇 서버에 메시지 전달
            const { data } = await http.post(
                // '/api/chatbot', 
                apiUrl,
            {
                lang: language,
                options: payload,
            });

            console.log('SEND_INTENT.data', data);

            // 숨기고 
            context.commit('HIDE_LOADING'); 

            // 서버응답 표시
            context.commit('ADD_CONVERSATION', chatbotChatHandler(data.contents)); // 발화표시

            // context.commit('UPDATE_OPTIONS', data.options || null);
        },

        // 서버에 발화를 전달
        async SEND_MESSAGE(context, payload) {
            // 사용자 입력창 초기화
            console.log('SEND_MESSAGE', payload);
            
            context.commit('UPDATE_MESSAGE', '');

            let message = '';
            let options = {};

            if (typeof payload === 'string') {  // 그냥 문자열을 보낸 경우
                message = payload;
                message = message.trim();
                if (!message) {
                    return;
                }
    
            } else {
                message = payload.message;
                options = payload.options || {};
            }

            let msg = context.state.message || message;

            // todo : 서버쪽으로 로직을 넘겨야 한다.
            // 상담사면 상담 쪽으로 대화 전달
            if (context.state.mode === dictionary.writerType.COUNSELOR) {
                context.dispatch('SEND_CHAT_TO_COUNSELOR', { msg, 
                    callback: () => context.commit('ADD_CONVERSATION', userChatHandler(msg)) 
                });
                return;
            }

            // 숨김이 아니면 대화에 유저 메세지 추가
            if(msg)
            {
                context.commit('ADD_CONVERSATION', userChatHandler(options.replace ? options.replace : msg));
            }

            // 로딩 표시
            context.commit('SHOW_LOADING', dictionary.writerType.CHATBOT);

            // 챗봇 서버에 메시지 전달
            console.log('options>', options);

            if(options.isSend == false)
            {
                context.commit('HIDE_LOADING'); 
                return;
            }

            const { data } = await http.post(
                // '/api/chatbot', 
                apiUrl,
            {
                text: msg,
                lang: language,
                options,
            });

            console.log('SEND_MESSAGE.data', data);

            // 로그인이 아니면 UI처리하고 리턴
            if (data.intent === dictionary.intentType.USER_NOT_LOGIN) {
                // window.location = data.url;
                uiHandler.showClosePopup();
                context.dispatch('CALL_USER_LOGOUT'); 
                return;
            }

            // 상담사 연결이 필요한 경우 이노믹스(상담) 처리
            const isConsultingRequest = data.intent === dictionary.intentType.CSConnectCustomerService;
            if (isConsultingRequest) 
            {
                const MAX_PREVIOUS_CONVERATION_SIZE = 300;
                const previousConversationObject = context.state.conversation[context.state.conversation.length - 2];
                const previousConversation = previousConversationObject.contents
                  .filter((i) => i.type === dictionary.componentType.NormalMessage)
                  .map((i) => i.message.replaceAll('<br />', '\n'))
                  .join('\n\n')
                  .substr(0, MAX_PREVIOUS_CONVERATION_SIZE);
                const isOverSize = previousConversation.length === MAX_PREVIOUS_CONVERATION_SIZE;
                context.dispatch('HANDLE_CONSULTING', { text: data.text, previousConversation: isOverSize ? `${previousConversation}...` : previousConversation });
                return;
            }
           
           
            // 숨기고 
            context.commit('HIDE_LOADING'); 

            // 서버응답 표시
            context.commit('ADD_CONVERSATION', chatbotChatHandler(data.contents)); // 발화표시

            context.commit('UPDATE_OPTIONS', data.options || null);
        },
    },
};
