import moment from 'moment';
import dictionary from '../dictionary';

// 사용자 발화표시
export const userChatHandler = (msg, pivot = null, time = null) => ({
        type: dictionary.writerType.USER,
        contents: [
            {
                type: dictionary.componentType.NormalMessage,
                data: {
                    message: msg,
                }
            },
        ],
        date: {
            pivot: pivot || moment().format('a'),
            time: time || moment().format('hh:mm'), // hh -> 00 ~ 12, HH 00 ~ 24
        },
    });

export const counselorChatHandler = (msg, pivot = null, time = null) => ({
        type: dictionary.writerType.COUNSELOR,
        contents: [
            {
                type: dictionary.componentType.NormalMessage,
                data: {
                    message: msg,
                }
            },
        ],
        date: {
            pivot: pivot || moment().format('a'),
            time: time || moment().format('hh:mm'),
        },
    });

export default (response, pivot = null, time = null) => {
    if (typeof response === 'string') {
        return {
            type: dictionary.writerType.CHATBOT,
            contents: [
                {
                    type: dictionary.componentType.NormalMessage,
                    data: {
                        message: response,
                    }
                },
            ],
            date: {
                pivot: pivot || moment().format('a'),
                time: time || moment().format('hh:mm'),
            },
        };
    }

    return {
        type: dictionary.writerType.CHATBOT,
        contents: response,
        date: {
            pivot: pivot || moment().format('a'),
            time: time || moment().format('hh:mm'),
        },
    };
};