// import 'es6-promise/auto';
import Vue from 'vue';
import VueI18n from 'vue-i18n';
import moment from 'moment';
import chatbotChatHandler, { userChatHandler } from '@/utils/chatbotChatHandler';
import enomixHandlers from '@/utils/enomixHandlers';
import uiHandler from '@/utils/uiHandlers';
import commonHandler from '@/utils/commonHandler';
import language from '@/utils/getLanguage';
import analyticsHandler from '@/utils/analyticsHandler';
import messages from '@/assets/messages';
import { router } from '@/routes/routes';

import dictionary from './dictionary';
import axios from './utils/axios';
import App from './App.vue';
import '@/style/cover.css';

Vue.use(VueI18n);

Vue.config.productionTip = false;
Vue.prototype.$http = axios;
Vue.prototype.$dictionary = dictionary;
Vue.prototype.$moment = moment;
Vue.prototype.$chatbotChatHandler = chatbotChatHandler;
Vue.prototype.$userChatHandler = userChatHandler;
Vue.prototype.$enomixHandlers = enomixHandlers;
Vue.prototype.$commonHandler = commonHandler;
Vue.prototype.$uiHandler = uiHandler;
Vue.prototype.$lang = language;
Vue.prototype.$analyticsHandler = analyticsHandler;

window.language = language;

const i18n = new VueI18n({
  locale: language,
  messages,
});

window.addEventListener('message', (event) => {
  if (event.origin.startsWith('http://localhost:8080')) {
    if (event.data === 'SSG_LOGOUT') {
      alert('logout');
    }
  }
});

new Vue({
  i18n,
  render: (h) => h(App),
  router,
}).$mount('#app');