const callResponse = (responseType, params= {}, text='') => {
    const payload = {
        responseType,
        params,
        text,
    };
    window.chatbot.store.dispatch('CALL_RESPONSE', payload);
}

const sendIntent = (intent, params, text='') => {
    const payload = {
        intent,
        params,
        text,
    };
    window.chatbot.store.dispatch('SEND_INTENT', payload);
}

const sendMessage = (message, isSend = true) => {
    const payload = {
        message,
        options: {
            isSend,
        }
    }
    window.chatbot.store.dispatch('SEND_MESSAGE', payload);
}

window.cb_callResponse = callResponse; // (responseType, params= {}, text='')
window.cb_sendIntent = sendIntent; // (intent, params, text='')
window.cb_sendMessage = sendMessage; //(message, isSend = true)

export default {
    callResponse,
    sendMessage,
    sendIntent,
}