<template>
  <div class="chatContents" tabindex="0" style="background:#e9ebfb">
    <div
        :class="getWho(item.type).className"
        v-bind:key="index"
        v-for="(item, index) in conversation"
    >
      <ChatbotIcon
          :who="getWho(item.type).iconName"
          v-if="item.type !== $dictionary.writerType.USER"
      />

      <div class="chatTxtCont">
        <template
            v-for="(content, index2) in item.contents"
        >
          <DynamicLoader :info="content" :key="index2"></DynamicLoader>
        </template>
      </div>

      <div class="chatTime">
        {{$commonHandler.translateTimePivot(item.date.pivot)}} <em>{{item.date.time}}</em>
      </div>
    </div>
    <ChatLoading />
  </div>
</template>

<script>
import ChatLoading from '@/components/ChatLoading';
import DynamicLoader from '@/components/DynamicLoader';
import ChatbotIcon from '@/components/ChatbotIcon';
import callback from '@/project/callback';

export default {
  name: 'Chat',
  props: {
    conversation: Array,
    isScrollDown: Boolean,
  },
  components: {
    ChatLoading,
    DynamicLoader,
    ChatbotIcon,
  },
  mounted() {
    if(window.chatbot === undefined) window.chatbot = {}
    window.chatbot.store = this.$store;

    callback.mounted();
    this.$uiHandler.downScroll(this.$store.state.scrollHeight);
  },
  watch: {
    conversation() {
      if (!this.isScrollDown) {
        return;
      }

      const scrollHeight = this.$uiHandler.downScroll();
      this.$store.commit('SET_SCROLL_HEIGHT', scrollHeight);
    },
  },
  methods: {
    getWho(who) {
      switch (who) {
        case this.$dictionary.writerType.CHATBOT:
          return {
            className: 'chatType01',
            iconName: '챗봇',
          };

        case this.$dictionary.writerType.COUNSELOR:
          return {
            className: 'chatType02',
            iconName: '상담사',
          };

        default:
          return {
            className: 'chatType03',
            iconName: null,
          };
      }
    },    
  },
};
</script>