<template>
  <header class="chatHeader">
    <h1 class="titDep1 titChat" v-if="isChatbotMode">{{ $t('chatbot_header.chatbot_name') }}</h1>
    <h1 class="titDep1 titChat02" v-else>{{ $t('chatbot_header.counselor_name') }}</h1>
    <div class="sideBtn" v-if="isChatbotMode">
      <a @click="gohome()" class="chatHome">{{ $t('chatbot_header.home') }}</a>
      <!-- <button type="button" class="counseler" @click="$store.dispatch('SEND_MESSAGE', $t('chatbot_header.counselor_connection'))"> -->
      <button type="button" class="counseler" @click="onCounselor()">
        {{ $t('chatbot_header.counselor_connection') }}
      </button>
    </div>
    <div class="sideBtn" v-else>
      <!-- <button type="button" class="chatbot" @click="onChangeToChatbot">{{ $t('chatbot_header.home') }}</button> -->
      <button type="button" class="chatbot" @click="onChangeToChatbot">{{ $t('chatbot_header.home') }}</button>
    </div>
    <button type="button" @click="onClose()" class="closeC">닫기</button>
  </header>
</template>

<style scoped>
a{
  cursor:pointer;
}
</style>

<script>
export default {
  name: 'ChatbotHeader',
  computed: {
    isChatbotMode() {
      return this.$store.state.mode === this.$dictionary.writerType.CHATBOT;
    },
    disable() {
      return this.$store.state.disableChat;
    },
  },
  methods: {
    onCounselor() {
      this.$store.dispatch('SEND_MESSAGE', this.$t('chatbot_header.counselor_connection'));
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_13);
    },

    onClose() {
      this.$uiHandler.showClosePopup();
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_11);
    },

    onChangeToChatbot() {
      if (this.disable) {
        return;
      }
      this.$store.dispatch('EXIT_CONSULTING');
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_14);
    },
    gohome() {
      this.$store.dispatch('SEND_MESSAGE', this.$t('common.처음으로'));
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_12);
    },
  },
};
</script>