// 주문 가능 조회 모달 보이기
import $ from 'jquery';
import store from '../../vuex/store';

const showOrderPossibleTimeModal = (date, time) => {
   $('#order-time-search-modal').addClass('on');
   $('#order-time-search-modal-container > .dimmed').show();
    setOrderPossibleTimeModalDate(date);
    setOrderPossibleTimeModalTime(time);

    // 상태에서도 업데이트
    store.commit('SET_SET_ORDERTIME_DATE', date);
    store.commit('SET_SET_ORDERTIME_TIME', time);

    console.log(date, time);
    if (!date || !time) {
        setOrderTimeButtonDisable(true);
        setOrderTimeTooltipVisible(true);
    } else {
      setOrderTimeButtonDisable(false);
      setOrderTimeTooltipVisible(false);
    }
};

// 주문 가능 조회 모달 숨기기
const hideOrderPossibleTimeModal = () => {
    $('#order-time-search-modal').removeClass('on');
};

const setOrderPossibleTimeModalDate = (val) => {
    $('#inp_flyDate').val(val);
};

const setOrderPossibleTimeModalTime = (val) => {
    // $('#inp_flyTime').val(val);
    $('#inp_flyTime').val(val);
};

const getOrderPossibleTimeModalDate = () => $('#inp_flyDate').val() || '';

const getOrderPossibleTimeModalTime = () => $('#inp_flyTime').val() || '';

const setOrderTimeTooltipVisible = (visible) => {
  if (visible) {
    $('.order-time-search-modal-tooltip').show(); // Tooltip
  } else {
    $('.order-time-search-modal-tooltip').hide(); // Tooltip
  }
};

const setOrderTimeButtonDisable = (disable) => {
  if (disable) {
    $('#order-time-button').removeClass('action'); // button
  } else {
    $('#order-time-button').addClass('action'); // button
  }
};

export default {
  showOrderPossibleTimeModal,
  hideOrderPossibleTimeModal,
  setOrderPossibleTimeModalDate,
  setOrderPossibleTimeModalTime,
  getOrderPossibleTimeModalDate,
  getOrderPossibleTimeModalTime,
  setOrderTimeButtonDisable,
  setOrderTimeTooltipVisible,
};